import React, { Component } from 'react';
import _ from 'lodash';

import { Data } from './Data';
import CardList from './CardList';
import Preparare from './Preparare';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = { };
    this.state.cards = Data();
    this.state.activeState = true;
    this.state.stage = 0;
  }

  changeImage = (i) => {
    const { cards, activeState } = this.state;

    const actionSelect = (i) => {
      if (!activeState) {
        return;
      }
      cards[i].selected = !cards[i].selected;
      this.setState({ cards });
    }

    const actionFlip = () => {
      this.setState({ activeState: false });
      const x = _.findIndex(cards, { selected: true });
      const y = _.findLastIndex(cards, { selected: true });
      cards[x].selected = false;
      cards[y].selected = false;
      cards[x].back = false;
      cards[y].back = false;
      this.setState({ cards });
      flipTimer(x, y);
    }

    const flipTimer = (x, y) => {
      setTimeout(() => {
        cards[x].back = true;
        cards[y].back = true;
        if (cards[x].front === cards[y].front) { cards[x].deleted = true; cards[y].deleted = true; }
        if (_.filter(cards, { deleted: true }).length === 24) { this.setState({ stage: 0 }); }
        this.setState({ activeState: true });
        this.setState({ cards });
      }, 2000);
    }

    actionSelect(i);
    (_.filter(cards, { selected: true }).length === 2) && actionFlip();

  }

  stageChange = () => this.setState({ cards: Data(), stage: 1 });

  render = () => {
    const { cards, stage } = this.state;
    const { changeImage, stageChange } = this;
    const whereTo = stage === 1 ? <CardList cards={cards} changeImage={changeImage} /> :
                                  <Preparare stageChange={stageChange} />;
    return (whereTo);
  };

}

export default App;



