import React from 'react';

const Card = ({ card, changeImage }) => {
  const pathFront = `./img/${card.front}.jpg`;
  const pathBack = './img/back.png';

  const className = ['cell'];

  // if card is deleted, don't handle the click for the card
  if (card.deleted) { className.push('deleted'); changeImage = null; }
  if (card.back) { className.push('flip'); }
  if (card.selected) { className.push('selected'); }

  return (
    <div
      className={className.join(' ')}
      onClick={changeImage}
      role="presentation"
    >
      <div className="flipper">
        <img className="front" alt="" src={pathFront} />
        <img className="back" alt="" src={pathBack} />
      </div>
    </div>
  );
};

export default Card;



