import _ from 'lodash';
import shortid from 'shortid';

let tmp = ['00', '01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', 
           '00', '01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11'];

const Data = () => {
  tmp = _.shuffle(tmp);
  return _.map(tmp, (elem) => ({
    front: elem,
    back: true,
    deleted: false,
    selected: false,
    key: shortid.generate(),
  }));
}

export { Data };



